export default {
  
state: () => ({ 
    theme: '', 
    safeLogo: false,
    themesMenuCategory: '',
    displayThemeName: '',
    backgroundURL: '',
    backgroundPicture: false,  
}),

  mutations: {
    changeTheme: (state, theme) => {
        state.theme = theme;
    },
    changeSafeLogo: (state, safeLogo) => {
        state.safeLogo = safeLogo;
    },
    changeThemesMenuCategory: (state, themesMenuCategory) => {
        state.themesMenuCategory = themesMenuCategory;
    },
    changeDisplayThemeName: (state, displayThemeName) => {
        state.displayThemeName = displayThemeName;
    },
    changeBackgroundURL: (state, backgroundURL) => {
        state.backgroundURL = backgroundURL;
    },
    changeBackgroundPicture: (state, backgroundPicture) => {
        state.backgroundPicture = backgroundPicture;
    },
  },

  //actions: { ... },

  //getters: { ... }
}