/* eslint-disable */

export default {

    getDepth: async function(passedDepth) {

        if (passedDepth == 'dark') {

            var returnedDepth = 

                `
                --boxShadowStandard: none;
                --boxShadowFlat: none;
                --boxShadowThinBorder: none;
                --boxShadowBanner: none;
                --messageShadow: none;
                --siteShadow: none;       
                ` 
        
        }

        else if (passedDepth == 'flat') {

            var returnedDepth = 

                `
                --boxShadowStandard: -1px 1px 5px var(--onSurfaceSolid);
                --boxShadowFlat: -1px 1px 3px var(--onSurfaceSolid);
                --boxShadowThinBorder: 0px 0px 1px var(--onSurfaceSolid);
                --boxShadowBanner: none;
                --messageShadow: var(--boxShadowThinBorder);
                --siteShadow: none;       
                ` 

        }

        else if (passedDepth == 'shallow') {

            var returnedDepth = 

                `
                --boxShadowStandard: -1px 1px 5px var(--onSurfaceSolid);
                --boxShadowFlat: -1px 1px 2px var(--onSurfaceSolid);
                --boxShadowThinBorder: 0px 0px 1px var(--onSurfaceSolid);
                --boxShadowBanner: 0px 0px 4px var(--onSurfaceSolid);
                --messageShadow: var(--boxShadowFlat);
                --siteShadow: var(--boxShadowFlat);        
                `  
        
        }

        else if (passedDepth == 'standard') {

            var returnedDepth = 

                `
                --boxShadowStandard: -1px 1px 5px var(--onSurfaceSolid);
                --boxShadowFlat: -1px 1px 3px var(--onSurfaceSolid);
                --boxShadowThinBorder: 0px 0px 1px var(--onSurfaceSolid);
                --boxShadowBanner: 0px 0px 9px var(--onSurfaceSolid);
                --messageShadow: var(--boxShadowFlat);  
                --siteShadow: var(--boxShadowFlat);              
                `  
        }

        else if (passedDepth == 'deep') {

            var returnedDepth =

                `
                --boxShadowStandard: -1px 1px 6px var(--onSurfaceSolid);
                --boxShadowFlat: -1px 1px 4px var(--onSurfaceSolid);
                --boxShadowThinBorder: 0px 0px 1px var(--onSurfaceSolid);
                --boxShadowBanner: 0px 0px 9px var(--onSurfaceSolid);
                --messageShadow: var(--boxShadowFlat);  
                --siteShadow: var(--boxShadowFlat);              
                `  
        }

        return returnedDepth

    }
}

/* eslint-enable */



