<template>          
    <div class="themesMenu">

        <div id="themesMenuBackButton" @click.prevent="$emit('toggleThemesMenu')">      
            <arrowRightIcon id="themesMenuBackButton" class="themesMenuIcon"></arrowRightIcon>
        </div>

        <div class="themesMenuTitle">
            Themes
        </div>
        <ul style="list-style: none;">
        <li v-for="theme in themes" :key="theme.key" >          
            <div class="themesMenuItem" @click="changeTheme(theme.value.name)">{{theme.value.name}}</div>
        </li>
        </ul>
        
    </div>
</template>

<style lang="scss">

.themesMenu {
    box-sizing: border-box;
    position: fixed;
    background: var(--surface4);
    margin: 0rem 0rem 0rem 0rem;
    /*overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;*/
    top: 6.8rem;
    right: 0rem;
    width: auto;
    height: auto;
    min-height: 34rem;
    max-height: calc(100vh - 6.5rem);
    overflow: scroll;
    border-style: solid;
    border-width: 0.1rem;
    border-color: rgba(255,255,255, 0.0) var(--darkBorderColourOnly) var(--darkBorderColourOnly) var(--darkBorderColourOnly);
    border-radius: calc(var(--baseBorderRadius)*1) 0 calc(var(--baseBorderRadius)*1) calc(var(--baseBorderRadius)*1);
    box-shadow: var(--boxShadowStandard);
}
.themesMenuTitle {
    color: var(--onSurfaceBold);
    box-sizing: border-box;
    position: relative;
    font-size: calc(1.6rem * var(--largeText));
    font-weight: bold;
    padding: 1rem 4rem 1rem 0.5rem;
    margin: 1.5rem 1rem 0rem 1rem; 
    text-align: left; 
}
.themesMenuItem {
    color: var(--onSurface);
    box-sizing: border-box;
    position: relative;
    margin: 2rem 5rem 2rem 3rem;
    min-width: 11rem;
    font-size: calc(1.4rem * var(--largeText));
}
.themesMenuIcon {
    fill: var(--onSurfaceBold);
    stroke: var(--onSurfaceBold);
    position: absolute;
    right: 0.2rem;
    top: 1rem;
    width: 2.4rem;
    height: 2.4rem;
}
#themesMenuBackButton {
    z-index: 1;
}
/*#themesMenuMoreButton {
    left: 1.5rem;
}*/

</style>

<script>

import themeHandler from '../../../themes/themeHandler.js'

import allThemes from '../../../themes/allThemes.js'

export default {
    name: "themesMenu",
    data() {
        return {
            themes: {}
        }
    },
    computed: {
        themesMenuCategory() {
            return this.$store.state.theme.themesMenuCategory;
        },
    },
    methods: {
        applyCategory: async function(themesMenuCategory) {

            //let matchCategory = themesMenuCategory;
            let unfilteredThemes = await allThemes.getItems(); 

            if (themesMenuCategory == 'LightBasic') {
                this.themes = await unfilteredThemes.filter(unfilteredTheme => (unfilteredTheme.value.lightness == 'light' && unfilteredTheme.value.backgroundPicture == false));
            }
            else if (themesMenuCategory == 'LightImage') {
                this.themes = await unfilteredThemes.filter(unfilteredTheme => (unfilteredTheme.value.lightness == 'light' && unfilteredTheme.value.backgroundPicture == true));
            }
            else if (themesMenuCategory == 'DarkBasic') {
                this.themes = await unfilteredThemes.filter(unfilteredTheme => (unfilteredTheme.value.lightness == 'dark' && unfilteredTheme.value.backgroundPicture == false));
            }
            else if (themesMenuCategory == 'DarkImage') {
                this.themes = await unfilteredThemes.filter(unfilteredTheme => (unfilteredTheme.value.lightness == 'dark' && unfilteredTheme.value.backgroundPicture == true));
            }
            
            //return this.themes
          
        },

        changeTheme: async function(newTheme) {
            if (newTheme == 'blank') {

                await this.showAlertMessage('Coming Soon')
                
            }

            else {

                console.log(newTheme);

                await themeHandler.setTheme(newTheme);
                //await this.$store.commit('changeDisplayThemeName', newTheme);

                /*let vm = this.$parent
                while(vm) {
                vm.$forceUpdate();
                vm = vm.$parent
                }*/

            }
        },

        /* LEARNING ONLY !!! getFileNames: async function() {
            var requireFileNames = require.context(
            '../../../themes/',
            false,
            /_[\w-]+\.js$/
            );

            console.log(requireFileNames.keys());

        }*/
    },
    async created() {
        await this.applyCategory(this.themesMenuCategory); 
    },
}
</script>
