/* eslint-disable */

export default {

    getButton2Colour: async function(passedButton2) {

        if (passedButton2 == 'primary') {

            var returnedButton2 = 

                `
                --button2Background: var(--primary);
                --button2Content: var(--background);
                --button2Border: var(--darkBorder);
                --button2BoxShadow: none;
              
                ` 
        }

        else if (passedButton2 == 'primaryVariant') {

            var returnedButton2 =         

                `
                --button2Background: var(--primaryVariant);
                --button2Content: var(--background);
                --button2Border: var(--darkBorder);
                --button2BoxShadow: none ;       
                ` 
        } 

        else if (passedButton2 == 'secondary') {

            var returnedButton2 =  

                `
                --button2Background: var(--secondary);
                --button2Content: var(--background);
                --button2Border: var(--darkBorder);
                --button2BoxShadow: none;          
                `

        }

        else if (passedButton2 == 'secondaryVariant') {

            var returnedButton2 = 

                `
                --button2Background: var(--secondaryVariant);
                --button2Content: var(--background);
                --button2Border: var(--darkBorder);
                --button2BoxShadow: none ;             
                `
                
        }

        else if (passedButton2 == 'onBackground') {

            var returnedButton2 = 

                `
                --button2Background: var(--onSurfaceSolid);
                --button2Content: var(--background);
                --button2Border: var(--darkBorder);
                --button2BoxShadow: none ;             
                ` 

        }

        else if (passedButton2 == 'surface') {

            var returnedButton2 =        

                `
                --button2Background: var(--surface5);
                --button2Content: var(--onSurfaceBold);
                --button2Border: var(--darkBorder);
                --button2BoxShadow: var(--boxShadowThinBorder);              
                ` 
                
        }

        else if (passedButton2 == 'primaryOnSurface') {

            var returnedButton2 =        

                `
                --button1Background: var(--surface5);
                --button1Content: var(--primary);
                --button1Border: var(--darkBorder);
                --button1BoxShadow: var(--boxShadowThinBorder);              
                ` 
                
        }  
        
        else if (passedButton2 == 'secondaryOnSurface') {

            var returnedButton2 =        

                `
                --button1Background: var(--surface5);
                --button1Content: var(--secondary);
                --button1Border: var(--darkBorder);
                --button1BoxShadow: var(--boxShadowThinBorder);              
                ` 
                
        } 

        return returnedButton2
    
    }
    
}

/* eslint-enable */



