export default {
  
state: () => ({     
    topMenu: false, 
    themesTopMenu: false,
    themesMenu: false
}),

  mutations: {
    changeTopMenu: (state, topMenu) => {
        state.topMenu = topMenu;
    },
    changeThemesTopMenu: (state, themesTopMenu) => {
        state.themesTopMenu = themesTopMenu;
    },
    changeThemesMenu: (state, themesMenu) => {
      state.themesMenu = themesMenu;
  },
  },

  //actions: { ... },

  //getters: { ... }
}