<script>

    import themesTopMenu from './theme-menus/themesTopMenu.vue'

    export default {

        components: {
            themesTopMenu
           // 'appearanceMenu': () => import('../components/appearanceMenu')
        },
        name: "topMenu",
        /*data() {
            return {
                themesTopMenuActive: false,
            }
        },*/
        computed: {

            topMenuVisible() {
            return this.$store.state.menus.topMenu;
            },
            themesTopMenu() {
            return this.$store.state.menus.themesTopMenu;
            },

        },   
        methods: {
            
            /*toggleThemesTopMenu: async function(themesTopMenu) {
                //await this.timeout(5);
                await this.$store.commit('changeThemesTopMenu', !themesTopMenu);
            },*/

            removeTopMenu: async function() {
                await this.$store.commit('changeTopMenu', false);
                await this.$store.commit('changeThemesTopMenu', false);
                //this.themesTopMenuActive = false
            },
            
            documentClick: async function(e) {
                try {

                    let el12 = this.$refs.topMenu;
                    let target12 = e.target;
                    if (el12 == (target12)) { 
                    this.$store.commit('changeThemesMenu', false);
                    await this.timeout(50);
                    this.$store.commit('changeThemesTopMenu', false);
                    await this.timeout(50);
                    this.$store.commit('changeTopMenu', false);                   
                    //this.$store.commit('changeTopMenu', false)
                    //this.themesTopMenuActive = false
                    }

                }
                catch (err) {
                    return
                }
            }, 
        },

        async created() {

            await document.addEventListener('click', this.documentClick);
            
        },

        destroyed() {

            document.removeEventListener('click', this.documentClick);

        },

    }

</script>

