/* eslint-disable */

export default {

    getLightness: async function(passedLightness) {

        if (passedLightness == 'dark') {

            var returnedLightness = 

                `
                --surface1: rgb(calc(var(--surfaceR) + 0),calc(var(--surfaceG) + 0),calc(var(--surfaceB) + 0));
                --surface2: rgb(calc(var(--surfaceR) + 5),calc(var(--surfaceG) + 5),calc(var(--surfaceB) + 5));
                --surface3: rgb(calc(var(--surfaceR) + 10),calc(var(--surfaceG) + 10),calc(var(--surfaceB) + 10));
                --surface4: rgb(calc(var(--surfaceR) + 15),calc(var(--surfaceG) + 15),calc(var(--surfaceB) + 15));
                --surface5: rgb(calc(var(--surfaceR) + 20),calc(var(--surfaceG) + 20),calc(var(--surfaceB) + 20));
                --onSurfaceSolid: rgb(calc(var(--onSurfaceR) * 1),calc(var(--onSurfaceG) * 1),calc(var(--onSurfaceB) * 1));
                --onSurfaceBold: rgb(calc(var(--onSurfaceR) * 0.87),calc(var(--onSurfaceG) * 0.87),calc(var(--onSurfaceB) * 0.87));
                --onSurface: rgb(calc(var(--onSurfaceR) * 0.6),calc(var(--onSurfaceG) * 0.6),calc(var(--onSurfaceB) * 0.6));
                --onSurfaceDisabled: rgb(calc(var(--onSurfaceR) * 0.38),calc(var(--onSurfaceG) * 0.38),calc(var(--onSurfaceB) * 0.38));
                --darkBorder: 0.1rem solid rgba(255,255,255,0.06);
                --darkBorderColourOnly: rgba(255,255,255,0.06);
                --bannerBorderWidth: 0.1rem;
                ` 
        }

        if (passedLightness == 'light') {

            var returnedLightness = 
        
                `
                --surface1: rgb(calc(var(--surfaceR) + 0),calc(var(--surfaceG) + 0),calc(var(--surfaceB) + 0));
                --surface2: var(--surface1);
                --surface3: var(--surface1);
                --surface4: var(--surface1);
                --surface5: var(--surface1);
                --onSurfaceSolid: rgb(calc(var(--onSurfaceR) * 1),calc(var(--onSurfaceG) * 1),calc(var(--onSurfaceB) * 1));
                --onSurfaceBold: rgb(calc(var(--onSurfaceR) * 1),calc(var(--onSurfaceG) * 1),calc(var(--onSurfaceB) * 1));
                --onSurface: rgb(calc(var(--onSurfaceR) * 1),calc(var(--onSurfaceG) * 1),calc(var(--onSurfaceB) * 1));
                --onSurfaceDisabled: rgb(calc(var(--onSurfaceR) * 1),calc(var(--onSurfaceG) * 1),calc(var(--onSurfaceB) * 1));
                --darkBorder: 0rem solid rgba(0,0,0,0);
                --darkBorderColourOnly: transparent;
                --bannerBorderWidth: 0rem;

                `
        }

        return returnedLightness
    
    }
}

/* eslint-enable */



